<template>
  <section class="up content">
    <h1>Tjenestestatus</h1>

    <table>
      <tr v-for="svc in services" :key="svc.id">
        <td><a :href="`http://oppetid.app.uib.no/?tjeneste=${svc.domain}`"><uptime-status :domain="svc.domain"/></a></td>
        <td><a :href="svc.link">{{svc.domain}}</a></td>
        <td><router-link :to="`/node/${svc.id}`">{{svc.title}}</router-link></td>
      </tr>
    </table>
  </section>
</template>

<script>
import tkdata from '@/tkdata';
import UptimeStatus from '@/components/UptimeStatus.vue';

export default {
  data() {
    return {
      services: [],
    };
  },
  created() {
    tkdata().then(data => {
      const services = [];
      const domains = new Set();
      Object.values(data.service).forEach(svc => {
        const domain = svc.link.domain;
        if (!domain || domains.has(domain))
          return;
        if (svc.info.service_state == 'avsluttet')
          return;
        domains.add(domain);
        services.push({
          id: svc.id,
          title: svc.title,
          domain,
          link: svc.link.home,
        });
      });
      services.sort((a,b) => a.domain.localeCompare(b.domain, 'nb'));
      this.services = services;
    });
  },
  components: {
    UptimeStatus,
  }
};
</script>

<style scoped>
td {
  padding: 2px 5px;
  vertical-align: top;
}
tr:hover {
  background: white;
  transition: background 0.5s;
}
</style>